.fl-fb-blink-red {
  animation-name: blinkOutline;
  animation-duration: 0.3s;
  animation-iteration-count: 2;
  animation-direction: reverse; }

@keyframes blinkOutline {
  0% {
    outline: 1px solid transparent; }
  50% {
    outline: 1px solid rgba(250, 0, 0, 0.8); }
  100% {
    outline: 1px solid transparent; } }

.fl-fb-ControlBar {
  display: flex;
  flex-flow: row wrap;
  align-items: stretch;
  justify-content: space-between;
  padding-bottom: 10px; }

.fl-fb-Fields {
  display: flex;
  flex-flow: column wrap;
  align-items: stretch; }

.fl-fb-Field {
  display: flex;
  flex-flow: row nowrap;
  align-content: stretch;
  justify-content: space-between;
  position: relative;
  padding: 0 0 0 15px;
  margin: 5px 0;
  border-radius: 3px;
  border: 1px solid transparent;
  transition: transform .2s ease-out;
  z-index: 1; }
  .fl-fb-Field:hover {
    background-color: #ebf4fc;
    border-color: #a2a2a2; }

.fl-fb-Field--configuration-visible {
  z-index: 2;
  background-color: #ebf4fc;
  border-color: #a2a2a2; }

.fl-fb-Field-editable {
  background-color: transparent;
  border: 0;
  min-width: 80%; }
  .fl-fb-Field-editable:hover, .fl-fb-Field-editable:active, .fl-fb-Field-editable:focus {
    outline: 0;
    color: #009688; }

.fl-fb-Field--dragging {
  box-shadow: 0 0 15px -5px rgba(0, 0, 0, 0.8);
  z-index: 4;
  background-color: #ebf4fc; }

.fl-fb-Field-content {
  width: 100%;
  padding-right: 15px;
  padding-bottom: 20px; }

.fl-fb-Field-option {
  display: block; }
  .fl-fb-Field-option > * {
    margin-left: 10px; }

.fl-fb-Field-option-text {
  font-weight: bold; }

.fl-fb-Field-TextArea {
  resize: none; }

.fl-fb-Field-sidebar {
  display: flex;
  flex-flow: column nowrap;
  align-items: stretch;
  min-height: 100%;
  padding: 5px 10px 20px;
  border-radius: 0 3px 3px 0;
  text-align: center;
  background-color: #eee;
  visibility: hidden;
  opacity: 0; }
  .fl-fb-Field:hover .fl-fb-Field-sidebar,
  .fl-fb-Field--configuration-visible .fl-fb-Field-sidebar {
    visibility: visible;
    opacity: 1; }

[class*=fl-fb-Field-sidebar-btn] {
  background-color: transparent;
  border: 0;
  opacity: .8;
  margin: 2px 0;
  border-radius: 3px;
  padding: .3em .5em;
  visibility: visible;
  opacity: 1; }
  [class*=fl-fb-Field-sidebar-btn]:hover {
    opacity: 1;
    background-color: #d5d5d5; }
  [class*=fl-fb-Field-sidebar-btn]:active {
    background-color: #c8c8c8; }
  [class*=fl-fb-Field-sidebar-btn]:active, [class*=fl-fb-Field-sidebar-btn]:focus {
    outline: 0; }

.fl-fb-Field-sidebar-btn-delete {
  margin-top: auto;
  color: #d9534f; }

.fl-fb-Field--configuration-visible .fl-fb-Field-sidebar-btn-config {
  visibility: hidden;
  opacity: 0;
  transition: all .2s ease, visibility 0s; }

.fl-fb-Field-configuration {
  background-color: #eee;
  width: calc(100% + 2px);
  padding: 5px 15px 2px;
  border: 1px solid transparent;
  border-width: 0 1px 1px;
  visibility: hidden;
  opacity: 0;
  transition: all 0.2s ease, visibility 0s;
  position: absolute;
  right: -1px;
  bottom: 3px;
  z-index: 2;
  transform: translateY(100%);
  border-radius: 0 0 3px 3px; }
  .fl-fb-Field--configuration-visible .fl-fb-Field-configuration {
    visibility: visible;
    opacity: 1;
    border-color: #a2a2a2; }

.fl-fb-Field-configuration-buttons,
.fl-fb-Field-configuration-options {
  display: flex;
  flex-flow: row nowrap;
  align-items: stretch;
  margin-bottom: 10px; }

.fl-fb-Field-configuration-options-optionDisabled.fl-fb-Field-configuration-options-optionDisabled {
  margin-left: 5px;
  margin-right: 5px; }

.fl-fb-Field-configuration-elementName {
  margin: auto 0 auto auto;
  font-weight: bold;
  transform: translateY(0.1em);
  color: #999;
  padding: 6px 12px;
  border-radius: 3px;
  background-color: #eee; }

.fl-fb-Field-configuration-btn-ok:hover, .fl-fb-Field-configuration-btn-ok:active, .fl-fb-Field-configuration-btn-ok:focus {
  outline: 0 !important; }

[class*=fl-fb-Field-configuration-options-btn] {
  background-color: transparent;
  color: #999;
  border: 0; }

.fl-fb-Field-configuration-options-input {
  min-width: 50%;
  text-indent: 5px; }

.fl-fb-Field--transparent {
  background-color: transparent;
  border: 0;
  min-width: 50%; }

.fl-fb-Field-config {
  position: relative;
  background-color: #eee;
  padding: 10px 15px;
  top: 20px;
  left: -15px;
  width: calc(100% + 15px + 15px); }

.fl-fb-Field-config-btn {
  color: #999;
  padding: 0 6px;
  background: transparent;
  border: 0; }
  .fl-fb-Field-config-btn:hover, .fl-fb-Field-config-btn:active, .fl-fb-Field-config-btn:focus {
    outline: 0 !important; }
  .fl-fb-Field-config-btn:hover {
    color: gray; }
  .fl-fb-Field-config-btn:active {
    color: #333333; }

.fl-fb-Field-config-captionInput {
  min-width: 400px;
  display: inline-block;
  padding: 2px 5px; }

.fl-fb-Field-config-valueInput {
  width: 50px;
  display: inline-block;
  padding: 2px 5px;
  margin-right: 5px; }

.fl-fb-Field-dateslot-day,
.fl-fb-Field-dateslot-month,
.fl-fb-Field-dateslot-year {
  width: 1.7em;
  display: inline-block;
  min-width: 0;
  margin: 0 .1em;
  text-align: center; }

.fl-fb-Field-dateslot-year {
  width: 2.8em; }

.fl-fb-ui-switch {
  display: inline-block;
  margin: 0 5px; }
  .fl-fb-ui-switch .fl-fb-ui-switch-toggle {
    position: absolute;
    margin-left: -9999px;
    visibility: hidden; }
  .fl-fb-ui-switch .fl-fb-ui-switch-toggle + label {
    display: inline-block;
    vertical-align: middle;
    position: relative;
    cursor: pointer;
    outline: none;
    user-select: none; }
  .fl-fb-ui-switch input.fl-fb-ui-switch-toggle-round + label {
    padding: 2px;
    width: calc( 20px * 2);
    height: 20px;
    background-color: #dddddd;
    border-radius: 20px; }
  .fl-fb-ui-switch input.fl-fb-ui-switch-toggle-round + label:after,
  .fl-fb-ui-switch input.fl-fb-ui-switch-toggle-round + label:before {
    display: block;
    position: absolute;
    top: 1px;
    left: 1px;
    bottom: 1px;
    content: ""; }
  .fl-fb-ui-switch input.fl-fb-ui-switch-toggle-round + label:before {
    right: 1px;
    background-color: #f1f1f1;
    border-radius: 20px;
    transition: background 0.4s; }
  .fl-fb-ui-switch input.fl-fb-ui-switch-toggle-round + label:after {
    width: calc( 20px - 2px);
    background-color: #fff;
    border-radius: 100%;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
    transition: margin 0.4s; }
  .fl-fb-ui-switch input.fl-fb-ui-switch-toggle-round:checked + label:before {
    background-color: #63A8EB; }
  .fl-fb-ui-switch input.fl-fb-ui-switch-toggle-round:checked + label:after {
    margin-left: 20px; }

.fl-fb-Field textarea {
  min-height: 80px; }
